/**
 * API URL yardımcı fonksiyonları
 */

/**
 * API URL'yi protokole ve ortama göre ayarlayan yardımcı fonksiyon
 * @returns {string} API URL
 */
export const getApiUrl = () => {
  // Her zaman .env dosyasındaki URL'yi kullan
  let apiUrl = process.env.REACT_APP_API_URL || 'http://localhost:8080';
  
  // Eğer sayfa HTTPS üzerinden yüklendiyse ve API URL'si HTTP ise, HTTPS'e çevir
  if (window.location.protocol === 'https:' && apiUrl.startsWith('http:')) {
    apiUrl = apiUrl.replace('http:', 'https:');
  }
  return apiUrl;
};

/**
 * API endpoint URL'si oluşturan yardımcı fonksiyon
 * @param {string} endpoint - API endpoint (örn. "/api/chat")
 * @returns {string} Tam API URL'si
 */
export const getApiEndpoint = (endpoint) => {
  const baseUrl = getApiUrl();
  // Eğer baseUrl boşsa (göreceli URL kullanılıyorsa), doğrudan endpoint'i döndür
  if (!baseUrl) return endpoint;
  
  // URL birleştirme - çift slash olmamasını sağla
  if (baseUrl.endsWith('/') && endpoint.startsWith('/')) {
    return `${baseUrl}${endpoint.substring(1)}`;
  }
  
  if (!baseUrl.endsWith('/') && !endpoint.startsWith('/')) {
    return `${baseUrl}/${endpoint}`;
  }
  
  return `${baseUrl}${endpoint}`;
};
